@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,600;1,300;1,600&family=Oswald:wght@200;300;400;500;600;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
*{
    padding: 0;
    margin: 0;
}

body{
  /* margin: 0px 5px; */
    background: #fff;
    color: #222;
    font-family: 'Montserrat', sans-serif;
/* font-family: 'Oswald', sans-serif;
font-family: 'Roboto', sans-serif;
font-family: 'Ubuntu', sans-serif; */
font-weight: 300;
}
@media screen and (max-width:1000px) {
  .wrapper {margin: 20px auto; }
   }
 @media screen and (min-width:1000px) {
    .wrapper {margin: 50px auto;}
     }
.wrapper {
  width: 100%;
max-width: 1280px;
z-index: 9999;

}

header{
  position: relative;
  z-index: 9999;
 
}
@media screen and (max-width:1000px) {
  header .logo {margin: 0px 10px; padding-top: 0px;}
   }

header .logo{
    font-weight: 600;
    /* font-size: 20px; */
    font-size: 1.2em;
  
}
@media screen and (max-width:1000px) {
  header .presentation { margin: 20px 0; height: 70vh;padding-top: 50px; }
   }
   @media screen and (min-width:1000px) {
    header .presentation { margin: 50px 0; height: 500px; }
     }
header .presentation{
    /* margin: 50px 0; */
    background:url("./img/1.jpg") no-repeat;
    background-size: cover;
    background-position: center center;
    background-blend-mode:multiply;
    background-color: #777;
    width: 100%;
    max-height: 500px;
    /* height: 500px; */
    /* height: 80vh; */
    /* height: 50%; */
    position: relative;
    z-index:-1;
}
header .presentation::after{
  content: "Convenient, Quality, Accessible";  
  position: absolute;
  top:120px;
  left:50px;
  font-weight: 600;

  /* font-size: 25px; */
  font-size: 1.5em;
  color: aqua;
}
header .presentation::before{
    content: "Հարմար, որակով, մատչելի";  
    position: absolute;
    top:180px;
    left:50px;
    font-weight: 300;
    /* font-size: 15px; */
    font-size: 1em;
    color: aqua;
  }
  @media screen and (max-width:1000px) {
    header ul.nav {margin: 0px 10px; z-index: 9999;text-shadow: 2px 2px 4px #000000;
      color: #00FFFF} 
     }
  header ul.nav{
float: right;
list-style: none;


  }
  @media screen and (max-width:1000px) {
   header ul.nav li {display: flex; flex-direction:row-reverse;
}
     }
     @media screen and (min-width:1000px) {
      header ul.nav li  {display: inline;}
       }
  header ul.nav li{
    /* display: inline; */
    margin-left: 25px;
    cursor: pointer;
    transition: opacity 500ms ease;
  }
  header ul.nav li:hover{
    opacity: 0.5;
  }
  header .shop-cart-button{
    float: right;
    cursor: pointer;
    transition: color, transform 500ms ease;
  }
  header .shop-cart-button.active,
  header .shop-cart-button.hover
  {
    color: #ca5355;
    transform: scale(1.5);
  }
  @media screen and (max-width:1000px) {
    header .shop-cart{width: 80%;}
     }
     @media screen and (min-width:1000px) {
      header .shop-cart{width: 450px}
       }
  header .shop-cart{
position: absolute;
top:30px;
right: 0;
/* width: 450px; */
/* min-height: 100px; */
padding: 20px;
background: #f5f5f5;
z-index: 1;
box-shadow: 5px 5px 15px -7px rgba(0,0,0,0.75);
-webkit-box-shadow: 5px 5px 15px -7px rgba(0,0,0,0.75);
-moz-box-shadow: 5px 5px 15px -7px rgba(0,0,0,0.75);

  }
  header .shop-cart .item{
    width: 100%;
    float: left;
    padding: 10px 0;
    padding-bottom: 0px;

  }
  
  header .shop-cart .item img{
  width: 70px;
  float: left;
  margin-right: 20px; 
  }
  header .shop-cart .item h2{
    /* font-size: 20px;
     */
    margin-bottom: 10px;
    font-size: 1.2rem;
  }
  header .shop-cart .item p{
color: #777;
font-weight: 600;

  }
  header .shop-cart .empty h2{
/* font-size: 20px; */
font-size: 1․2em;
/* margin-bottom: 10px; */
padding-bottom: 10px;
  }
  header .shop-cart .item .delet-icon{
    color: #ca5355;
    float: right;
   /* right: 50px; */
    position: relative;
    top:-25px;
    cursor: pointer;
    transition: color, transform 500ms ease;
  }
  header .shop-cart .item .delet-icon:hover{
    color: #ca2222;
    transform: scale(1.5);
  }
  header .shop-cart .summa{
    float: left;
    width:100%;
    font-weight: 600;
    /* font-size: 20px; */
    font-size: 1.2em;
    padding-top: 20px;
  }
  @media screen and (max-width:1000px) {
    .categories div {margin-right: 30px; margin: 0px 10px;
    
  }
     }
  @media screen and (min-width:1000px) {
      .categories div {margin-right: 15px;}
       }
  .categories div{
    display: inline-block;
    background: #f2f2f2;
    border-radius:20px ;
    padding: 10px 20px;
    margin-bottom: 25px;
    /* margin-right: 15px; */
  cursor: pointer;
border: 1px solid transparent;  
transition: all 500ms ease;
}
.categories div:hover{
border-color: silver;
transform: scale(1.1);
}
  main{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
    font-size: 0.7em;

  }
  @media screen and (max-width:600px) {
     main .item {width:45%;}
  }
  @media screen and (min-width:600px) {
    main .item {width:30%;}
 }
  main .item{
    /* width: 30%; */
    margin-bottom: 50px;
    background: #f5f5f5;
    overflow: hidden;
    position: relative;
    padding-bottom: 50px;
  }
  .full-item{
position: fixed;
top:0;
left: 0;
right: 0;
bottom: 0;
background-color: rgba(0,0,0,0.75);
z-index: 9999;
overflow: auto;

  }
  @media screen and (max-width:600px) {
    .full-item>div {width:80%;}
 }
 @media screen and (min-width:600px) {
  .full-item>div {width: 600px;}
}
  .full-item>div{
    /* width: 600px; */
    position: relative;
    margin: 10% auto;
    padding: 40px 30px;
    background: #f2f2f2;
    border-radius: 7px;

  }
  main .item img,
  .full-item img {
    width: 100%;
    border-radius: 10px 10px 0 0;
    transition: transform 500ms ease;
  }
  main .item img:hover,
  .full-item  img:hover{
transform: scale(1.05);
  }
  main h2, main p, main b,
  .full-item h2,
  .full-item p,
  .full-item b{
    margin: 10px 20px;
    color: #555;

  }
  main b,.full-item b{
    color:#5fa37a;
  
  }
  main .add-to-cart,
  .full-item .add-to-cart{
    position: absolute;
    right: 20px;
    bottom: 20px;
    background: #ca5355;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: #fff;
    cursor: pointer;
    border-radius: 50%;
    font-weight: 600;
    transition: transform 500ms ease;

  }
  main .add-to-cart:hover,
  .full-item .add-to-cart:hover{
    transform: scale(1.5) translateY(-5px);
  }
  footer{
    text-align: center;
    margin-top: 100px;
  }